body {
  font-family: 'Nanum Gothic', Arial, sans-serif; /* 바람의나라 느낌의 폰트 */
  margin: 0;
  padding: 0;
  background: url("back1.png") no-repeat top fixed; /* 픽셀 아트 느낌의 배경 */
  background-size: cover; /* 픽셀 느낌을 살리기 위해 크기 고정 */
  color: #f5e4c3; /* 고풍스러운 글자 색상 */
}

body::-webkit-scrollbar {
  display: none; /* 웹킷 브라우저(Chrome, Edge, Safari)에서 스크롤바 숨김 */
}

.app {
  padding: 20px;
  max-width: 900px;
  margin: 20px auto;
  background: rgba(20, 20, 20, 0.8); /* 반투명 검정색 배경 */
  border: 4px solid #b39260; /* 금색 테두리 */
  border-radius: 8px;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.5); /* 깊이감을 위한 그림자 */
}

.title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
  color: #e8c374; /* 금빛 글자 */
  text-shadow: 2px 2px 0 #4b3820; /* 텍스트 그림자 */
}

.region-buttons,
.dungeon-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-bottom: 20px;
}

.region-button,
.dungeon-button {
  padding: 10px 20px;
  font-size: 14px;
  font-family: 'Nanum Gothic', Arial, sans-serif;
  border: 2px solid #8d6346; /* 동양적인 느낌의 브라운 컬러 테두리 */
  background-color: #3b2b20; /* 어두운 갈색 배경 */
  color: #f5e4c3;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 0 #4b3820; /* 텍스트 그림자 */
}

.region-button.active,
.dungeon-button.active {
  background-color: #b34234; /* 전통적인 붉은색 */
  color: #fff;
  border-color: #e2a572; /* 밝은 테두리 */
}

.region-button:hover,
.dungeon-button:hover {
  background-color: #e46a5c; /* 더 밝은 붉은색 */
  color: #fff;
}

.debuffs {
  margin-top: 20px;
  text-align: center;
  color: #f5e4c3; /* 고풍스러운 글자 색상 */
  font-size: 16px;
}

.debuff-label {
  display: block;
  margin-bottom: 10px;
}

.monster-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.monster-card {
  padding: 15px;
  background-color: #3b2b20; /* 어두운 갈색 배경 */
  border-radius: 8px;
  border: 2px solid #8d6346; /* 테두리 추가 */
  text-align: center; /* 중앙 정렬 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* 그림자 */
}

.monster-image {
  width: 90px;
  height: 90px;
  object-fit: contain; /* 이미지를 카드 안에 맞게 비율 유지하며 조정 */
  margin-bottom: 10px; /* 이미지와 텍스트 간격 */
  border-radius: 5px;
  border: 0px solid #ffffff; /* 가벼운 테두리 */
}

.monster-card ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.monster-card h4 {
  font-size: 16px;
  margin-bottom: 10px;
  color: #e8c374; /* 금빛 글자 */
}

.monster-card li {
  font-size: 14px;
  margin-bottom: 5px;
  color: #f5e4c3; /* 고풍스러운 글자 색상 */
}

.google-sheet-link {
  margin-right: 10px;
  font-size: 16px;
  text-decoration: none;
  color: #e8c374; /* 금색 느낌의 링크 */
  transition: color 0.3s ease;
}

.google-sheet-link:hover {
  color: #fff;
}

.update-log-button {
  padding: 5px 15px;
  font-size: 14px;
  border: 2px solid #8d6346;
  background-color: #3b2b20;
  color: #f5e4c3;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.update-log-button:hover {
  background-color: #b34234;
  color: #fff;
}

.update-log {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 15px;
  background-color: #3b2b20;
  border: 2px solid #8d6346;
  border-radius: 8px;
  line-height: 1.6;
  font-size: 14px;
  color: #f5e4c3;
  white-space: pre-wrap;
}
